<template>
  <div class="bg">
    <div class="root">
      <result-header :searchKey="searchKey" :searchTypeName="getSearchTypeName"
                  :demo="getDemo" :desc="getDesc" :detailDesc="getDetailDesc" :execDemo="execDemo">
      </result-header>

      <result-loading v-show="state === 0"></result-loading>
      <result-content v-show="state !== 0" :rawData="rawData" :retry="retry"></result-content>

    </div>

  </div>
</template>

<script>
  import CommonSearchResultHeader from './CommonSearchResultHeader.vue'
  import CommonSearchResultContent from './CommonSearchResultContent.vue'
  import CommonSearchResultLoading from './CommonSearchResultLoading'
  import bus from '../common/bus'

  const SEARCH_STATE = {
    LOADING: 0,
    SUCCEED: 1,
    FAILED: 2,
    TIMEOUT: 3,
  }

  export default {
    name: '',
    components: {
      'result-header': CommonSearchResultHeader,
      'result-content': CommonSearchResultContent,
      'result-loading': CommonSearchResultLoading,
    },
    props: {
      searchType: {
        type: Number,
        default: gconst.SearchType.PHONE_STATE
      },
      searchKey: {
        type: String,
        default: '15811024484'
      },
      isDemo: {
        type: Boolean,
        default: false,
      },
      demo: {
        type: String,
        default: '15812345678'
      },
      desc: {
        type: String,
        default: '简单的描述'
      },
      detailDesc: {
        type: String,
        default: '详细的描述1\n详细的描述1\n详细的描述1\n详细的描述1\n详细的描述1\n'
      },
      rawData: {
        type: Object,
        default: () => {}
      },
    },
    data () {
      return {
        state: SEARCH_STATE.LOADING,
      }
    },
    methods: {
      getSearchType: function () {
        return '手机号查询'
      },

      execDemo: function () {
        let demo = this.getDemo

        bus.$emit('createNewTag', {
          searchType: this.searchType,
          searchKey: demo,
          isDemo: true
        })
      },

      failedReason () {
        switch(this.state) {
          case SEARCH_STATE.LOADING:
            return "加载中..."
          case SEARCH_STATE.FAILED:
            return this.rawData.reason || "请求失败!"
        }
      },

      retry () {
        this.state = SEARCH_STATE.LOADING

        gddmgr.searchRequest(this.searchType, this.searchKey, this.isDemo).then(res => {

          this.rawData = gutils.clone(res)

          if (this.rawData && this.rawData.code === 1) {
            this.state = SEARCH_STATE.SUCCEED
          } else {
            this.state = SEARCH_STATE.FAILED
          }
        }, (err) => {
          this.rawData = gutils.clone(err)
          console.log("---------ThirdView catch:" + JSON.stringify(this.rawData ))

          this.state = SEARCH_STATE.FAILED
        }).catch(err => {
          this.rawData = gutils.clone(err)
          console.log("---------ThirdView catch:" + JSON.stringify(this.rawData ))
          this.state = SEARCH_STATE.FAILED
        })
      }
    },
    computed: {
      getRawData () {
        console.log("-------this.rawData:" + JSON.stringify(this.rawData))
        return this.rawData
      },

      getSearchTypeName () {
        let data = gddmgr.getScreenData(this.searchType)
        if (data && data.title)
          return data.title

        return '未知类型'
      },

      getDemo () {
        let data = gddmgr.getScreenData(this.searchType)
        if (data && data.demo)
          return data.demo

        return '-'
      },

      getDesc () {
        return gddmgr.getScreenData(this.searchType).desc
      },

      getDetailDesc () {
        return gddmgr.getScreenData(this.searchType).detailDesc
      }
    },
    created () {

      this.retry()
    }
  }
</script>

<style scoped>
  .bg {
    width: 100%;
    height: 100%;
    background: #F0F0F0;
    overflow: auto; /* 垂直滚动条 */
  }

  .root {
    padding: 20px 50px 0px;
  }
</style>



