<!-- 通用显示结果：关键字部分 -->
<template>
  <div>
    <div class="header_title">帮助信息</div>

    <div class="user_info">

      <div class="row row_group">
        <span class="col_left">关键字:</span>
        <span class="col_right">{{ this.searchKey }}</span>
      </div>

      <div class="row row_group">
        <span class="col_left">查询类型:</span>
        <span class="col_right">{{ this.searchTypeName }}</span>
      </div>

      <div class="row row_group">
        <span class="col_left">查询示例:</span>
        <span class="col_right">
          <span>{{ this.demo }}</span>
          <span class="enable_interactive left_space" @click="execExample()">[运行示例]</span>
        </span>
      </div>

      <div class="row row_group">
        <span class="col_left">功能描述:</span>
        <span class="col_right">
        <span>
          <span>{{ this.desc }}</span>
          <span class="enable_interactive left_space" @click="showDetail = !showDetail">{{ showDetail ? "隐藏详情" : "查看详情" }}</span></span>
      </span>
      </div>

      <!-- 详细信息 -->
      <div class="row row_group" v-show="showDetail">
      <span class="detail">
        <vue-markdown class="markdown">{{ this.detailDesc }}</vue-markdown>
      </span>
      </div>

    </div>
  </div>

</template>

<script>
  import VueMarkdown from 'vue-markdown'

  export default {
    name: 'CommonSearchResultHeader',
    components: {
      'vue-markdown': VueMarkdown
    },
    props: {
      searchType: {
        type: Number,
        default: gconst.SearchType.PHONE_STATE
      },
      searchTypeName: { type: String },
      searchKey: { type: String },
      demo: { type: String, default: '15800001111' },
      desc: { type: String },
      detailDesc: { type: String },
      execDemo: { type: Function, default: () => {} },
    },
    data: function () {
      return {
        showDetail: false,
      }
    },
    methods: {
      execExample: function () {
        if (this.execDemo) {
          this.execDemo()
        }
      }
    },
    computed: {
      multiTexts: function () {
        return this.detailDesc.split('\n')
      }
    }
  }
</script>

<style scoped>
  @import "../../../static/css/main.css";
  @import "../search.css";

  .row {
    display: flex;
    flex-direction: row;
  }

  .row_group {
    padding: 10px 20px;
    font-size: 16px;
    font-family: MicrosoftYaHei;
    color: rgba(144,144,144,1);
    /*line-height: 20px;*/
  }

  .col_left{
    text-align: right;
    margin-right: 20px;
    width: 130px;
  }
  .col_right{
    text-align: left;
    flex: 1;
    display: flex;
    color: #666666;
  }

  .left_space {
    margin-left: 16px;
  }

  .detail {
    flex: 1;
    background: #EEEEEE;
    margin: 0px 0px 0px 60px;
    padding: 20px 15px;
    border-radius: 5px;
  }

  .markdown {
    /*font-size: 16px;*/
    font-family: MicrosoftYaHei;
    color: rgba(144,144,144,1);
  }

  li {
    margin-bottom: 5px;
  }

</style>



