<!--
通用: 通用搜索框部分
-->
<template>
 <div class="search_bar_root">
     <el-select class="select-type"  size="medium" v-model="selectContent" @change="channgeSearchType">
       <el-option-group
         class="search_select_panel_box"
         v-for="group in searchGroups"
         :key="group.title"
         :label="group.title">
         <el-option
           v-for="item in group.data"
           :key="item"
           :label="getSearchName(item)"
           :value="item"
           @change="channgeSearchType(item)"
         >
         </el-option>
       </el-option-group>
     </el-select>

     <el-row class="row_container">
       <el-col v-for="(item, idx) in searchContent" :key="idx" :span="24/searchContent.length">

         <!-- 快递单号查询: 快递公司 第一个参数-->
         <el-select size="medium" class='param_select' v-if='isLogiticsUI(idx)' v-model="company" placeholder="Select">
           <el-option
             v-for="item in companies"
             :key="item.com"
             :label="item.com"
             :value="item.no">
           </el-option>
         </el-select>

         <!-- 区号查询: 第二个参数 -->
         <el-select size="medium" class='param_select' v-else-if='isPhoneAreaCodeUI(idx)' v-model="areaCode" placeholder="Select">
           <el-option
             v-for="item in areaCodes"
             :key="item.value"
             :label="item.label"
             :value="item.value">
           </el-option>
         </el-select>

         <el-input size="medium" v-else :placeholder="getPlaceholder(idx)" v-model="searchContent[idx]"></el-input>
       </el-col>

     </el-row>

     <el-button class='button' size="medium" type="primary" icon="el-icon-search" @click="startSearch()">查询</el-button>
   </div>

</template>

<script>
  const DEF_SEARCH_TYPE = gconst.SearchType.BUSINESS
  import { mapState } from 'vuex'
  import bus from './bus'

  export default {
    props: {
      initSearchType: {
        type: Number,
      },
      headers: {
        type: Array,
        default: () => []
      },
      datas: {
        type: Array,
        default: () => []
      },
      fixAttr: {      // 修正序列
        type: Object,
        default: () => {
          return {
            deletes: [],
            seqs: [],     // 指定顺序，可以指定部分
            custom: {},
          }
        }
      }
    },
    watch: {
      searchType: function (newval, oldVal) {
        console.log('new: %s, old: %s', newval, oldVal)
//        this.searchType = newval
        this.selectContent = this.searchType
        this.resetData()
      }
    },
    data: function () {
      return {
        searchContent: [],
        selectContent: this.searchType,
        autoFillDemoDatas: true,
        input: '测试内容',

        company: 'sf',      // 快递单号查询：公司名称
        areaCode: '',       // 手机区号定位：区号选择

        // [{ "com":"顺丰", "no":"sf" }, ... ]
        companies: [],
        areaCodes: [{
          value: 'Option1',
          label: 'Option1'
        }, {
          value: 'Option2',
          label: 'Option2'
        }, {
          value: 'Option3',
          label: 'Option3'
        }, {
          value: 'Option4',
          label: 'Option4'
        }, {
          value: 'Option5',
          label: 'Option5'
        }],
      }
    },
    created () {
      this.selectContent = this.searchType
      this.resetData()
    },
    methods: {

      isLogiticsUI (param_index) {
        return this.searchType === gconst.SearchType.LOGISTICS && param_index === 0
      },

      isPhoneAreaCodeUI (param_index) {
        return this.searchType === gconst.SearchType.PHONE_EREA_CODE && param_index === 1
      },

      // 获得每个编辑机的提示信息
      getPlaceholder (idx) {
        let sdata = gddmgr.getScreenData(this.searchType)
        if (sdata && sdata.placeHolder) {
          if (idx < sdata.placeHolder.length) {
            return sdata.placeHolder[idx]
          }
        }
        return '未知placeholder"'
      },

      // 检查参数的有效性
      checkParams () {
        let data = gddmgr.getScreenData(this.searchType)
        if (data && data.checkTypes) {
          let checks = data.checkTypes
          let params = this.searchContent
          // TODO by cjp: 检查参数的类型，是否满足要求
          for (let i = 0 ; i < checks.length ; i++) {
            let check = checks[i]
            let param = params[i]
            let check_result = gutils.checkParam(param, check)
            if (check_result.code !== gutils.ParamCheckError.OK) {
              this.$alert(check_result.reason, '错误提示', {
                confirmButtonText: '',
                center: true,
                type: 'error'
              })
              return false
            }
          }
        }
        return true
      },

      // 重置数据
      resetData () {
        if (this.autoFillDemoDatas) {
          let sd = gddmgr.getScreenData(this.searchType)
          if (sd && sd.demo)
            this.searchContent = gfuncs.splitSearchParams(sd.demo)
          // this.searchContent = ['15811024484']
        }


        if (this.companies.length <= 0) {
          // TODO: 请求玩过消息
          gapi.getLogisticsCompanies().then( res => {
            let data = res.data
            if (data && data.code === 1 && data.data && data.data.resultList) {
                this.companies = data.data.resultList
            } else {
              this.companies = [
                {
                  "com":"顺丰",
                  "no":"sf"
                },
                {
                  "com":"申通",
                  "no":"sto"
                },
//                {
//                  "com":"圆通",
//                  "no":"yt"
//                },
//                {
//                  "com":"韵达",
//                  "no":"yd"
//                },
//                {
//                  "com":"天天",
//                  "no":"tt"
//                },
//                {
//                  "com":"EMS",
//                  "no":"ems"
//                },
//                {
//                  "com":"中通",
//                  "no":"zto"
//                },
//                {
//                  "com":"汇通",
//                  "no":"ht"
//                },
//                {
//                  "com":"全峰",
//                  "no":"qf"
//                },
//                {
//                  "com":"德邦",
//                  "no":"db"
//                },
//                {
//                  "com":"国通",
//                  "no":"gt"
//                },
//                {
//                  "com":"如风达",
//                  "no":"rfd"
//                },
//                {
//                  "com":"京东快递",
//                  "no":"jd"
//                },
//                {
//                  "com":"宅急送",
//                  "no":"zjs"
//                },
//                {
//                  "com":"EMS国际",
//                  "no":"emsg"
//                },
//                {
//                  "com":"Fedex国际",
//                  "no":"fedex"
//                },
//                {
//                  "com":"邮政国内（挂号信）",
//                  "no":"yzgn"
//                },
//                {
//                  "com":"UPS国际快递",
//                  "no":"ups"
//                },
//                {
//                  "com":"中铁快运",
//                  "no":"ztky"
//                }
              ]
            }
          })

        }
      },

      startSearch () {
        if (!this.checkParams()) {
          return
        }

        let params_cnt = this.getParamsCheckTypes.length
        this.searchContent.splice(0, this.searchContent.length - params_cnt)
        let str_params = gfuncs.getSearchParamsString(this.searchContent)

        if (this.$route.path.indexOf('home') >= 0) {
          this.$router.push({
            name: 'searchCenter',
            // path: '/page/LocalQuery',
            params: {
              searchType: this.searchType,
              searchKey: str_params
            }
          })

        } else {
          console.log("------------------createNewTag emit:" + str_params)
          bus.$emit('createNewTag', {
            searchType: this.searchType,
            searchKey: str_params
          })
        }
      },

      channgeSearchType (value) {
        this.$store.dispatch('search/setSearchType', value)

        this.resetData()
      },

      // 获得查询分类的名称
      getSearchName (search_type) {
        let title = gddmgr.getScreenTitle(search_type)
        return title
      },
    },
    computed: {
      ...mapState('search', [
        'searchType'
      ]),

      searchGroups () {
        return gconst.SearchGroup
      },

      getParamsCheckTypes () {
        let sdata = gddmgr.getScreenData(this.searchType)
        if (sdata && sdata.checkTypes)
          return sdata.checkTypes
        return []
      },
    },

  }
</script>

<style>
  @import "../search.css";

  .search_bar_root {
    width: 100%;
    display: flex;
    align-items: center;
  }
  .row_container {
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
  }
  .param_select {
    display: flex;
    flex: 1;
  }


  .el-select-dropdown {
    max-width: 1000px;
  }
  .el-select-dropdown__wrap {
    max-height: 800px;
  }
  .el-select-dropdown__list {
    padding: 10px;
  }
  .search_select_panel_box {
    overflow: hidden;
    padding: 0 0 10px;
  }
  .search_select_panel_box:not(:last-of-type) {
    padding-bottom: 10px;
    margin-bottom: 10px;
  }
  .search_select_panel_box .el-select-group__title {
    font-size: 16px;
    color: rgba(46,46,48,1);
    margin: 0 auto;
    font-weight: bold;
  }
  .search_select_panel_box:not(:last-of-type)::after {
    bottom: 0;
  }
  .el-select-group .el-select-dropdown__item {
    float: left;
    color: rgba(150,151,155,1);
    line-height: 28px;
    font-size: 14px;
  }
  .el-select-dropdown__item.selected {
    font-weight: lighter;
  }
</style>



