<template>
  <div class="result_box search_result_margin_top" @click="next">
    <vue-loading :type="type" color="gray" :size="{ width: (width || 100) + 'px', height: (height || 100) + 'px' }"></vue-loading>
    <div class="search_result_tips">{{ title }}</div>
  </div>
</template>

<script>
  import { VueLoading } from 'vue-loading-template'
  let types = ["balls", "bars", "beat", "bubbles", "cylon", "spin", "spiningDubbles"]

  export default {
    name: 'CommonSearchResultLoading',
    components: {
      VueLoading
    },
    props: {
      loadingType: {
        type: String,
        default: '',
      },
      title: {
        type: String,
        default: '加载中...'
      },
      width: {
        type: Number,
      },
      height: {
        type: Number,
      }
    },
    data () {
      return {
        type: '',
      }
    },
    created () {
      this.type = this.loadingType
      if (!types.includes(this.type)) {
        let idx = Math.floor(Math.random() * types.length)
        this.type = types[idx]
      }
    },
    methods: {
      next () {
        let index = types.indexOf(this.type) || 0
        index += 1
        if (index > types.length - 1) {
          index = 0
        }

        this.type = types[index]
      }
    },
    computed: {
      getLoadingType () {
        if (!types.includes(this.type)) {
          let idx = Math.floor(Math.random() * types.length)
          this.type = types[idx]
        }

        return this.type
      }
    }
  }
</script>

<style>
  @import "../search.css";

</style>



