<template>

  <div class="header">
    <!-- logo -->
    <div class="headerLeft" @click="toBackHome"><div class="collapse-btn"><i class="headerLogo"></i></div></div>

    <!--&lt;!&ndash; 返回查询中心 &ndash;&gt;-->
    <!--<div class="go_back_search" @click="goBackSearch" v-if="returnSearchPageBlock">-->
      <!--<p><i class="el-icon-back"></i>返回查询中心</p>-->
    <!--</div>-->

    <div class="search-container">
      <common-search-bar v-if='showHeader' class="common-search-bar"></common-search-bar>
    </div>

    <div class="header-right">
      <div class="wide_screen">
        <span class="el-dropdown-link">{{ getName }}</span>
        <el-button type="text" @click="personalCenter"><i class="el-icon-upload"></i> 个人中心</el-button>
        <el-button type="text"><i class="el-icon-upload"></i> 收藏夹</el-button>
        <el-button type="text" @click="loginOut"><i class="el-icon-upload"></i> 退出</el-button>
      </div>
      <el-dropdown class="narrow_screen" size="medium" trigger="hover">
        <span class="el-dropdown-link">{{ getName }} <i class="el-icon-arrow-down el-icon--right"></i></span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item><el-button type="text" @click="personalCenter"><i class="el-icon-upload"></i> 个人中心</el-button></el-dropdown-item>
          <el-dropdown-item><el-button type="text"><i class="el-icon-upload"></i> 收藏夹</el-button></el-dropdown-item>
          <el-dropdown-item divided @click.native="loginOut"><el-button type="text" ><i class="el-icon-upload"></i> 退出</el-button></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
  import { mapGetters, mapActions, mapState } from 'vuex'
  import CommonSearchBar from './CommonSearchBar.vue'
  import bus from './bus'
  const DEF_SEARCH_TYPE = gconst.SearchType.SUPER_SEARCH

  export default {
    name: 'Header',
    components: {
      'common-search-bar': CommonSearchBar
    },
    props: {
      showHeader: {
        type: Boolean,
        default: true
      }
    },
    data () {
      return {
        name: '',
        searchContent: [],
        searchType: DEF_SEARCH_TYPE,
        selectContent: DEF_SEARCH_TYPE,
        autoFillDemoDatas: true
      }
    },
    computed: {
      ...mapState('search', [
        'currentChannel',
        'searchBarBlock',
        'returnSearchPageBlock'
      ]),
      ...mapGetters('me', ['getName']),
      getParamsCheckTypes () {
        let sdata = gddmgr.getScreenData(this.searchType)
        if (sdata && sdata.checkTypes)
          return sdata.checkTypes
        return []
      }
    },

    created () {

      this.judgmentPath()
    },

    mounted () {
      gstorage.reload()
    },

    methods: {

      toBackHome: function () {
        this.$router.push('/')
      },
      personalCenter () {
        this.$router.push('/user/info')
      },
      loginOut () {
        this.$confirm('', '确定要退出登录吗?', {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: '',
          center: true
        }).then(() => {
          localStorage.removeItem('isLogin')
          localStorage.removeItem('access_Token')
          this.$router.push('/login')
        }).catch(() => {
          return false
        })
      },
      // goBackSearch () {
      //   this.$router.push('/home')
      // },
      // 判断搜索栏/返回标识
      judgmentPath () {
        let searchPath = this.$route.path
        if (searchPath.indexOf('home') > 0) {
          // 搜索页面
          this.$store.dispatch('search/searchBar', true)
          this.$store.dispatch('search/goBackSearchPage', false)
        } else if (searchPath.indexOf('searchCenter') > 0) {
          // 搜索主页
          this.$store.dispatch('search/searchBar', false)
          this.$store.dispatch('search/goBackSearchPage', false)
        } else {
          // 其他页面
          this.$store.dispatch('search/searchBar', false)
          this.$store.dispatch('search/goBackSearchPage', true)
        }
      }
    }
  }
</script>
<style>
  .header {
    /*min-width: 800px;*/
    width: 100%;
    box-sizing: border-box;
    /*display: inline-block;*/
    display: flex;
    height: 80px;
    line-height: 80px;
    color: #3F4262;
    background: rgba(71,120,199,1);
  }
  .headerLeft {
    float: left;
    width: 200px;
  }
  .collapse-btn{
    float: left;
    padding: 0 21px;
    cursor: pointer;
  }
  .collapse-btn .headerLogo {
    background: url("../../assets/images/logo2.png") no-repeat 100%;
    width: 140px;
    height: 47px;
    vertical-align: middle;
    display: inline-block;
  }

  .normal-input {
    width: 100px;
  }


  /*
    头部中的搜索框样式
  */
  .search-container {
    display: flex;
    flex: 1;
    min-width: 300px;
    margin: 0 100px 0 0;
  }
  .search-container .select-type, .search-container .row_container  {
    height: 50px;
    line-height: 50px;
    background: #FFFFFF;
  }
  .search-container .search_bar_root .select-type .el-input--medium .el-input__inner {
    height: 40px;
    line-height: 40px;
    border-right: 2px solid #4778C7;
  }
  .search-container .search_bar_root .button {
    border-radius: 0;
    height: 50px;
  }
  .search-container .select-type .el-input--medium .el-input__inner  {
    border: none;
    border-radius: 0;
    color: #4778C7;
  }
  .search-container .search_bar_root .row_container .el-input--medium .el-input__inner {
    border-radius: 0;
    border: none;
    border-right: 2px solid #666666;
  }
  .search-container .search_bar_root .row_container .el-col:nth-last-child(1) .el-input--medium .el-input__inner {
    border: none;
  }

  .search-container .search_bar_root .button span {
    font-size: 16px;
  }
  /*
    end
  */

  .common-search-bar {
    flex: 1;
    width: 100%;
  }

  /*
  搜索框
  */
  .currentLocation {
    float: left;
    line-height: 80px;
    margin-left: 50px;
    color: #3F4262;
    font-size: 26px;
    font-family: MicrosoftYaHei-Bold;
  }
  .currentLocation .el-input-group {
    vertical-align: middle;
    background-color: #fff;
  }

  .currentLocation .input-with-select .el-input-group__prepend, .currentLocation .input-with-select .el-input-group__append {
    background-color: #fff;
    padding: 0 15px;
    border-radius: 0;
    color: rgba(71,120,199,1);
  }
  .currentLocation .input-with-select .el-input-group__prepend .el-select {
    padding-right: 10px;
  }
  .currentLocation .input-with-select .el-input-group__prepend .el-input__inner {
    min-width: 150px;
    max-width: 200px;
  }
  .currentLocation .input-with-select .el-input-group__append {
    width: 50px;
    padding: 0 8px;
    border: none;
  }
  .currentLocation .input-with-select .el-input-group__append span {
    overflow: hidden;
  }
  .currentLocation .input-with-select .el-input-group__append span .header_search_icon {
    font-weight: bold;
    background: url("../../assets/images/search/header_search_icon.png") no-repeat 100%;
    width: 25px;
    height: 25px;
    display: inline-block;
    vertical-align: middle;
  }
  .currentLocation .input-with-select .el-input-group__append .el-button {
    margin: 0 5px;
  }
  .currentLocation .el-input--small .el-input__inner {
    height: 30px;
    line-height: 30px;
    border-left: 2px solid rgba(71,120,199,1);
    border-right: 2px solid rgba(71,120,199,1);
    border-bottom: none;
    border-top: none;
    margin: 10px 0;
    min-width: 600px;
    font-size: 16px;
    color: rgba(144,144,144,1);
  }

  /*
  姓名 个人信息 收藏夹 退出
  */

  .header-right{
    padding-right: 30px;
    font-family:MicrosoftYaHei;
  }
  .header-right button {
    border-radius: 10px;
    color: rgba(231,231,231,1);
    font-size: 16px;
  }

  .user-avator img {
    display: block;
    width:40px;
    height:40px;
    border-radius: 50%;
  }
  .el-dropdown-link {
    padding-right: 20px;
    cursor: pointer;
    font-size: 16px;
    color: rgba(231,231,231,1);
  }

  /*.go_back_search {*/
    /*float: left;*/
    /*font-family:MicrosoftYaHei;*/
    /*color:rgba(255,255,255,1);*/
    /*font-size: 18px;*/
    /*cursor: pointer;*/
    /*margin-left: 40px;*/
  /*}*/
  /*.go_back_search p i {*/
    /*margin: 0 10px 0 0;*/
  /*}*/

  .narrow_screen {
    display: none;
  }

  @media (max-width: 1024px) {
    .wide_screen {
      display: none;
    }
    .narrow_screen {
      display: block;
      text-align: right;
    }
  }
</style>



