<!-- 通用显示结果：显示一组结果 -->
<template>
  <div class='user_info'>
    <span v-for='data in datas'>
      <span>{{ logData(data) }}</span>
      <div class='row_group'>
        <span class='col_left'>{{ data.kc + ':' }}</span>
        <span class='col_right'>
          <span>{{ data.v }} </span>
        </span>
      </div>
    </span>
  </div>
</template>

<script>
  export default {
    name: 'CommonSearchResultContentItem',
    props: {
      datas: {
        type: Array,
        default: () => [
          {
            'k': 'phone',
            'v': '15828225532',
            'kc': '手机号'
          }, {
            'k': 'address',
            'v': '山东烟台',
            'kc': '地址'
          }, {
            'k': 'areaCode',
            'v': '028',
            'kc': '区号'
          }, {
            'k': 'zipCode',
            'v': '610000',
            'kc': '邮政编码'
          }, {
            'k': 'prefix',
            'v': '158',
            'kc': '号段'
          }
        ]
      }
    },
    methods: {
      logData (dt) {
//        console.log('logData:' + JSON.stringify(dt))
      }
    },
    created () {
//      console.log('result item:' + JSON.stringify(this.datas))
    }
  }
</script>

<style scoped>
  @import '../../../static/css/main.css';

  .row {
    display: flex;
    flex-direction: row;
  }

  .row_group {
    padding: 10px 20px;
    font-size: 16px;
    font-family: MicrosoftYaHei;
    color: rgba(144,144,144,1);
    line-height: 20px;
    margin: 0px 40px;
    /*background: red;*/
  }

  .col_left{
    text-align: right;
    margin-right: 20px;
    display: inline-block;
    /*background: green;*/
    /*width: 130px;*/
  }
  .col_right{
    display: inline-flex;
    text-align: left;
    flex: 1;
    /*display: flex;*/
    color: #666666;
  }

  .left_space {
    margin-left: 16px;
  }

  .detail {
    flex: 1;
    background: #BFBFBF;
    margin: 0px 0px 0px 60px;
    padding: 10px;
    border-radius: 5px;
  }

  li {
    margin-bottom: 5px;
  }

</style>



