<!-- 通用显示结果：查询结果部分 -->
<template>
  <div class="result-container">

    <!-- 成功有数据 -->
    <div v-show="!onlyShowNoDataAndError && getResultMode === 0">
      <div v-for="(data, index) in this.rawData.data" :key="index">
        <div class="header_title">{{ data.title }}</div>
        <result-content-item :datas="data.data || []"></result-content-item>

        <div class="bottom_space"></div>
      </div>
    </div>

    <!-- 成功无数据 -->
    <div v-show="getResultMode === 1" class="result_box search_result_margin_top">
      <img class="img_vip" @click="retry" src="../../assets/images/search/result_nodata.png">
      <div class="search_result_tips">
        抱歉,没有查找到相关数据!
      </div>
    </div>

    <!-- 失败 -->
    <div v-show="getResultMode === 2" class="result_box search_result_margin_top">
      <img class="img_vip" @click="retry" src="../../assets/images/search/result_failed.png">
      <div class="search_result_tips">{{ this.getFailedReason }}
        <span class="enable_interactive" @click="retry">[点击重试]</span>
      </div>
    </div>

  </div>
</template>

<script>

  const RESULT_MODE = {
    RM_NORMAL: 0,     // 显示正常结果，有数据
    RM_NO_DATA: 1,    // 正常显示结果，没数据
    RM_ERR: 2,        // 有错误
  }

  import CommonSearchResultContentItem from './CommonSearchResultContentItem'
  let render_phone_state =
    {
      "code": 0,
      "reason": "",

      // 当前数据会存贮
      "dataType": 0,     //0正常数据(默认) 1模拟数据
      "hasNext": true,
      "totalRowNum":2,
      "translate": {
        "phone": "手机号",
        "name": "姓名",
        "address": "地址",
        "phoneStatus": "手机号状态",
        "duration": "在网时长",
        "prefix": "号段",
        "cardType": "号码类型",
        "operators": "运营商",
        "areaCode": "区号",
        "zipCode": "邮政编码"
      },
      data: [{
        "title": "基础数据",
        "data": [{
          "k": "phone",
          "v": "15828225532",
          "kc": "手机号"
        }, {
          "k": "address",
          "v": "山东烟台",
          "kc": "地址"
        }, {
          "k": "areaCode",
          "v": "028",
          "kc": "区号"
        }, {
          "k": "zipCode",
          "v": "610000",
          "kc": "邮政编码"
        }, {
          "k": "prefix",
          "v": "158",
          "kc": "号段"
        }]
      }, {
        "title": "手机状态",
        "data": [{
          "k": "phoneStatus",
          "v": "正常使用",
          "kc": "手机号状态"
        }]
      }, {
        "title": "在网时长",
        "data": [{
          "k": "duration",
          "v": "12-24个月",
          "kc": "在网时长"
        }]
      }]
    }

  export default {
    name: 'CommonSearchResultContent',
    components: {
      'result-content-item': CommonSearchResultContentItem
    },
    props: {
      onlyShowNoDataAndError: {     // 只显示没有数据和错误，应用于已经定制好显示界面了
        type: Boolean,
        default: false
      },

      rawData: {
        type: Object,
        default: () => {
          return {
            data: []
          }
        }
      },

      retry: {
        type: Function,
        default: () => {}
      }
    },

    data: function () {
      return {
        search_type: gconst.SearchType.PHONE_STATE
      }
    },

    methods: {
    },

    computed: {
      getResultMode () {
        if (this.rawData &&
            this.rawData.data &&
            this.rawData.data instanceof Array
        ) {
          if (this.rawData.data.length > 0)
            return RESULT_MODE.RM_NORMAL
          else
            return RESULT_MODE.RM_NO_DATA
        }

        return RESULT_MODE.RM_ERR
      },

      getFailedReason () {
        return this.rawData.reason || this.rawData.failure || '查询失败!'
      }
    },

    created () {
    }
  }
</script>


<style scoped>
  @import "../search.css";
  @import "../../../static/css/main.css";

  .result-container {
    min-width: 400px;
  }

  li {
    margin-bottom: 5px;
  }

  .bottom_space {
    margin-bottom: 30px;
  }


</style>



